import React, { Component } from 'react';
import LocalizedStrings from 'react-localization';
// eslint-disable-next-line
import { Route, Switch, Redirect, withRouter } from 'react-router-dom';
// eslint-disable-next-line
import * as util from 'util';
import './App.css';
import ScreenContext from './ScreenContext.js';
import AquaAerobicScreen from './AquaAerobicScreen.js';
import AquaIndexScreen from './AquaIndexScreen.js';
import AquatrainingScreen from './AquatrainingScreen.js';
import AquaotherScreen from './AquaotherScreen.js';
import AquaAerobicFieldServiceAppScreen from './AquaAerobicFieldServiceAppScreen.js';
import FieldServiceIndexViewScreen from './FieldServiceIndexViewScreen.js';
import DataSheet_forms from './DataSheet_forms.js';
import DataSheet_support from './DataSheet_support.js';
import DataSheet_other from './DataSheet_other.js';
import DataSheet_localizationSheet from './DataSheet_localizationSheet.js';


class App extends Component {
  constructor(props) {
    super(props);

    this.dataSheets = {};
    this.dataSheets['forms'] = new DataSheet_forms('forms', this.dataSheetDidUpdate);
    this.dataSheets['support'] = new DataSheet_support('support', this.dataSheetDidUpdate);
    this.dataSheets['other'] = new DataSheet_other('other', this.dataSheetDidUpdate);
    this.dataSheets['localizationSheet'] = new DataSheet_localizationSheet('localizationSheet', this.dataSheetDidUpdate);
    this.dataSheetLoaded = {};

    this.dataSlots = {};
    this.dataSlots['ds_activeLang'] = "en";

    this.updateLocalizationFromDataSheet(this.dataSheets['localizationSheet']);


    this.serviceOptions_forms = {
      dataSlots: this.dataSlots,
      servicePath: "",
      query: "",
    };
    this.dataSheets['forms'].appActions = this;
    this.dataSheets['forms'].dataSlots = this.dataSlots;
    
    this.serviceOptions_support = {
      dataSlots: this.dataSlots,
      servicePath: "",
      query: "",
    };
    this.dataSheets['support'].appActions = this;
    this.dataSheets['support'].dataSlots = this.dataSlots;
    
    this.serviceOptions_other = {
      dataSlots: this.dataSlots,
      servicePath: "",
      query: "",
    };
    this.dataSheets['other'].appActions = this;
    this.dataSheets['other'].dataSlots = this.dataSlots;
    

    this.state = {
      screenTransitionForward: true,
    }

  }

  windowDidResize = () => {
    let w = window.innerWidth;
    let formatId;
    if (w < 576) formatId = 'narrow-phone';
    else if (w < 768) formatId = 'wide-phone';
    else if (w < 1024) formatId = 'narrow-tablet';
    else formatId = 'wide-tablet';
    if (formatId !== this.state.screenFormatId) {
      this.setState({screenFormatId: formatId});
    }
  }

  componentDidMount() {
    this.windowDidResize();
    window.addEventListener('resize', this.windowDidResize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.windowDidResize);
  }

  isLoading() {
    return this.state.loading;
  }

  goToScreen = (screenId, baseScreenId, props) => {
    // Implemented with React Router's 'history' object.
    // 'baseScreenId' is set when inside a container such as a tab bar.
    let path = '/'+screenId;
    if (baseScreenId && baseScreenId.length > 0) {
      path = '/'+baseScreenId+path;
      props = {};
    }
    this.props.history.push(path, {...props});
    window.scrollTo(0, 0);
  }

  goBack = () => {
    // Implemented with React Router's 'history' object.
    this.props.history.goBack();
  }

  getDataSheet = (sheetId) => {
    // This method is the default implementation and could be customized by a state management plugin.
    return this.dataSheets[sheetId];
  }

  addToDataSheet = (sheetId, newRow, actionId) => {
    // This method is the default implementation and could be customized by a state management plugin.
    let sheet = this.dataSheets[sheetId];
    if (sheet && newRow) {
      let promise = sheet.addItem(newRow, this['serviceOptions_'+sheetId] || {});
      this.setState({});
      return promise;
    }
  }

  updateInDataSheet = (sheetId, row, actionId) => {
    // This method is the default implementation and could be customized by a state management plugin.
    let sheet = this.dataSheets[sheetId];
    if (sheet && row) {
      let promise = sheet.replaceItemByKey(row.key, row, this['serviceOptions_'+sheetId] || {});
      this.setState({});
      return promise;
    }
  }

  removeFromDataSheet = (sheetId, row) => {
    let sheet = this.dataSheets[sheetId];
    if (sheet && row) {
      let promise = sheet.removeItem(row, this['serviceOptions_'+sheetId] || {});
      this.setState({});
      return promise;
    }
  }

  updateDataSlot = (slotId, value, actionId) => {
    // This method is the default implementation and could be customized by a state management plugin.
    if (value === this.dataSlots[slotId])
      return;

    this.dataSlots[slotId] = value;

    if (slotId === 'ds_activeLang') {
      this.locStrings.setLanguage(value);
    }
    this.setState({});
  }

  dataSheetDidUpdate = (dataSheet) => {
    // This method is the default implementation and could be customized by a state management plugin.
    this.setState({});
  }

  updateLocalizationFromDataSheet = (dataSheet) => {
    const stringsObj = dataSheet.getStringsByLanguage();
    if (stringsObj && Object.keys(stringsObj).length > 0) {
      this.locStrings = new LocalizedStrings(stringsObj);
    } else {
      this.locStrings = new LocalizedStrings({en: {}});
    }
    this.locStrings.setLanguage(this.dataSlots['ds_activeLang']);
  }

  loadData_jsonsrc1 = (dataSheet, options, firstLoad) => {
    // This method was written by data plugin 'Generic JSON'.
    this.setState({loading: true});
    
    // clear any placeholder data before load
    if (firstLoad) {
    	dataSheet.items = [];
    }
    
    const fetchComplete = (err) => {
      if (err) {
        // This error handling comes from React Studio
        // and currently doesn't do anything useful.
        console.error('** Web service load failed: ', err);
      } else {
      }
      this.setState({loading: false});
    }
    
    const url = dataSheet.urlFromOptions(options);  // this method was written by the web service plugin
    
    const fetchOpts = {
      method: 'GET',
      headers: {},
    };
    
    fetch(url, fetchOpts)
      .then((response) => {
        if (response.status >= 400) {
          throw new Error("Server error: "+response.status);
        }
        return response.json();
      })
      .then((json) => {
        dataSheet.loadFromJson(json);
        fetchComplete(null, options);
      })
      .catch((exc) => {
        fetchComplete(exc, options);
      });
  }
  loadData_jsonsrc2 = (dataSheet, options, firstLoad) => {
    // This method was written by data plugin 'Generic JSON'.
    this.setState({loading: true});
    
    // clear any placeholder data before load
    if (firstLoad) {
    	dataSheet.items = [];
    }
    
    const fetchComplete = (err) => {
      if (err) {
        // This error handling comes from React Studio
        // and currently doesn't do anything useful.
        console.error('** Web service load failed: ', err);
      } else {
      }
      this.setState({loading: false});
    }
    
    const url = dataSheet.urlFromOptions(options);  // this method was written by the web service plugin
    
    const fetchOpts = {
      method: 'GET',
      headers: {},
    };
    
    fetch(url, fetchOpts)
      .then((response) => {
        if (response.status >= 400) {
          throw new Error("Server error: "+response.status);
        }
        return response.json();
      })
      .then((json) => {
        dataSheet.loadFromJson(json);
        fetchComplete(null, options);
      })
      .catch((exc) => {
        fetchComplete(exc, options);
      });
  }
  loadData_jsonsrc3 = (dataSheet, options, firstLoad) => {
    // This method was written by data plugin 'Generic JSON'.
    this.setState({loading: true});
    
    // clear any placeholder data before load
    if (firstLoad) {
    	dataSheet.items = [];
    }
    
    const fetchComplete = (err) => {
      if (err) {
        // This error handling comes from React Studio
        // and currently doesn't do anything useful.
        console.error('** Web service load failed: ', err);
      } else {
      }
      this.setState({loading: false});
    }
    
    const url = dataSheet.urlFromOptions(options);  // this method was written by the web service plugin
    
    const fetchOpts = {
      method: 'GET',
      headers: {},
    };
    
    fetch(url, fetchOpts)
      .then((response) => {
        if (response.status >= 400) {
          throw new Error("Server error: "+response.status);
        }
        return response.json();
      })
      .then((json) => {
        dataSheet.loadFromJson(json);
        fetchComplete(null, options);
      })
      .catch((exc) => {
        fetchComplete(exc, options);
      });
  }

  createImageUrlFromProp = (prop) => {
    if (prop instanceof Object) {
      if (prop.type != null && prop.type === 'image' && prop.path != null) {
        return "(null)"+prop.path;
      }
    }
    return prop;
  }

  render() {
    const makeScreen = (screenId, baseProps, atTop, forward, subpathId) => {
      let screenCtxProps = {
        ...baseProps,
        atTopOfScreenStack: atTop,
        transitionForward: forward,
        appActions: this,
        dataSheets: this.dataSheets,
        locStrings: this.locStrings,
        deviceInfo: {
          screenFormatId: this.state.screenFormatId
        },
        'ds_activeLang': this.dataSlots['ds_activeLang'],
      };
      let screen;
      switch (screenId) {
        case 'aquaAerobic':
          screen = <AquaAerobicScreen {...baseProps} />;
          break;
        case 'aquaIndex':
          screen = <AquaIndexScreen {...baseProps} />;
          break;
        case 'aquatraining':
          screen = <AquatrainingScreen {...baseProps} />;
          break;
        case 'aquaother':
          screen = <AquaotherScreen {...baseProps} />;
          break;
        case 'aquaAerobicFieldServiceApp':
          screen = <AquaAerobicFieldServiceAppScreen {...baseProps} />;
          break;
        case 'fieldServiceIndexView':
          screen = <FieldServiceIndexViewScreen {...baseProps} />;
          break;
        default:
          screen = null;
      }
      if (screen) {
        return <ScreenContext.Provider value={screenCtxProps}>{screen}</ScreenContext.Provider>;
      }
    }

    return (
      <div className="App">
        <Switch>
          <Route path="/" render={(props) => makeScreen('aquaAerobicFieldServiceApp', props.location.state, true, true)} exact />
          <Route path="/aquaAerobic" render={(props) => {
            return makeScreen('aquaAerobic', props.location.state, true, true);
          }} />
          <Route path="/aquaIndex" render={(props) => {
            return makeScreen('aquaIndex', props.location.state, true, true);
          }} />
          <Route path="/aquatraining" render={(props) => {
            return makeScreen('aquatraining', props.location.state, true, true);
          }} />
          <Route path="/aquaother" render={(props) => {
            return makeScreen('aquaother', props.location.state, true, true);
          }} />
          <Route path="/aquaAerobicFieldServiceApp" render={(props) => {
            return makeScreen('aquaAerobicFieldServiceApp', props.location.state, true, true);
          }} />
          <Route path="/fieldServiceIndexView" render={(props) => {
            return makeScreen('fieldServiceIndexView', props.location.state, true, true);
          }} />
          <Route render={(props) => makeScreen('aquaAerobicFieldServiceApp', props.location.state, true, true)} />
        </Switch>
      </div>
    );
  }
}
export default withRouter(App)
